import React, { useState, useEffect, useContext } from "react";
import "semantic-ui-css/semantic.min.css";
import "./App.css";
import bocadillo from "./images/bocadillo.png";
import chocolate from "./images/chocolate.png";
import coca from "./images/coca.png";
import cena from "./images/cena.png";
import imageCompression from "browser-image-compression";
import { storage } from "./firebase/firebase";

import { Carrito } from "./Contexto";
import {
  Container,
  Segment,
  Header,
  Statistic,
  Table,
  List,
  Popup,
  Image,
  Label,
  Button,
  Icon,
  Modal,
  Dimmer,
  Loader,
  Input,
  Divider,
  Dropdown,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";

function currencyFormat(num) {
  return "$" + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
}

function makeid(length) {
  var result = "";
  var characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}

function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const fechaA = a.Disponibles;
  const fechaB = b.Disponibles;

  let comparison = 0;
  if (fechaA > fechaB) {
    comparison = -1;
  } else if (fechaA < fechaB) {
    comparison = 1;
  }
  return comparison;
}

export default function Productos() {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState();
  const [buscar, setBuscar] = useState("");
  const [categoria, setCat] = useState("");
  const dummyImg = "https://react.semantic-ui.com/images/wireframe/image.png";
  const [previewImg, setPreviewImg] = useState(dummyImg);
  const [activeItem, setActiveItem] = useState("");
  const allInputs = { imgUrl: "" };
  const [imageAsFile, setImageAsFile] = useState("");
  const [imageAsUrl, setImageAsUrl] = useState(allInputs);
  const [subir, setSubir] = useState(false);
  const [saving, setSaving] = useState(false);
  const [nombre, setNombre] = useState();
  const [cant, setCant] = useState();
  const [precio, setPrecio] = useState();
  const [open, setOpen] = useState(false);
  const [openEdit, setOpenEdit] = useState(false);
  const [listaCat, setListaCat] = useState();
  const [newPCat, setNewPCat] = useState();
  const categorias = [
    { name: "Snacks", src: bocadillo, categ: "Snacks" },
    { name: "Dulces", src: chocolate, categ: "Dulces" },
    { name: "Bebidas", src: coca, categ: "Bebida" },
    { name: "Otros", src: cena, categ: "Otros" },
  ];
  const sacardata = async () => {
    setLoading(true);
    var data = [];
    let datas = [];
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[711905343];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;
    let lastr = rows.length + 1;
    var j, i;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setData(datas);
    console.log(datas);
    setLoading(false);
  };

  async function handleImageUpload(event, id) {
    console.log(id);
    if (event.target.files && event.target.files[0]) {
      const imageFile = event.target.files[0];
      var reader = new FileReader();

      var preview = document.getElementById(id);
      if (id === "nuevoProd") {
        preview = preview.getElementsByTagName("img")[0];
      }
      reader.onload = function (e) {
        preview.setAttribute("src", e.target.result);
      };

      reader.readAsDataURL(event.target.files[0]);
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 1920,
        useWebWorker: true,
      };
      try {
        const compressedFile = await imageCompression(imageFile, options);
        setImageAsFile((imageFile) => compressedFile);
      } catch (error) {
        console.log(error);
      }
      setSubir(true);
    } else {
      setSubir(false);
      setImageAsFile("");
      setImageAsUrl("");
      preview = document.querySelector(id);
      preview.setAttribute("src", "");
      var archivoSelect = document.querySelector("#anexoImg");
      archivoSelect.value = "";
    }
  }

  const handleFireBaseUpload = (e) => {
    if (subir) {
      if (imageAsFile === "") {
        console.error(
          `not an image, the image file is a ${typeof imageAsFile}`
        );
      }
      const uploadTask = storage
        .ref(`/images/${imageAsFile.name}`)
        .put(imageAsFile);
      uploadTask.on(
        "state_changed",
        (snapShot) => {
          //takes a snap shot of the process as it is happening
          console.log(snapShot);
        },
        (err) => {
          //catches the errors
          console.log(err);
        },
        () => {
          // gets the functions from storage refences the image storage in firebase by the children
          // gets the download url then sets the image from firebase as the value for the imgUrl key:
          storage
            .ref("images")
            .child(imageAsFile.name)
            .getDownloadURL()
            .then((fireBaseUrl) => {
              setImageAsUrl((prevObject) => ({
                ...prevObject,
                imgUrl: fireBaseUrl,
              }));
              console.log(fireBaseUrl);
            });
        }
      );
      setSubir(false);
    }
  };

  const registrar = async () => {
    setSaving(true);
    console.log(activeItem);
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[711905343];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    let lastr = rows.length + 1;
    var j;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      if (sheet.getCell(j, 0).value === activeItem.id) {
        const precio = sheet.getCell(j, 2);
        const imagen = sheet.getCell(j, 3);
        const inventario = sheet.getCell(j, 5);
        const bodega = sheet.getCell(j, 8);
        const categoria = sheet.getCell(j, 4);
        if (imageAsUrl.imgUrl) {
          imagen.value = imageAsUrl.imgUrl;
        }
        categoria.value = activeItem.cat;
        precio.value = parseFloat(activeItem.price);
        inventario.value = parseFloat(activeItem.cant);
        bodega.value = parseFloat(activeItem.bodega);
        await sheet.saveUpdatedCells();
        break;
      }
    }
    setImageAsUrl(allInputs);
    setData();
    setSaving(false);
  };

  const registrarNuevo = async () => {
    setSaving(true);
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[711905343];
    var i;
    var pID;
    var checkID = 1;
    do {
      pID = makeid(8);
      checkID = data.filter((item) => {
        return item.Product_Id === pID;
      }).length;
    } while (checkID != 0);

    const newData = await sheet.addRow({
      Product_Id: pID,
      Product_Name: nombre,
      Price: precio,
      Disponibles: cant,
      Image: imageAsUrl.imgUrl,
      Categoria: newPCat,
    });
    setImageAsUrl(allInputs);
    setData();
    setSaving(false);
  };

  useEffect(() => {
    !data && sacardata();
    if (data) {
      var temp = categorias;
      let templista = [];
      var i;
      for (i = 0; i < temp.length; i++) {
        templista.push({
          key: temp[i].name,
          value: temp[i].name,
          text: temp[i].name,
        });
      }
      setListaCat(templista);
      console.log(temp);
    }
  }, [data]);

  useEffect(() => {
    subir && handleFireBaseUpload();
  }, [subir]);

  return (
    <Container>
      <Dimmer active={loading || saving} page>
        <Loader>{saving ? "Saving" : "Loading"}</Loader>
      </Dimmer>

      {activeItem && (
        <Modal
          closeOnEscape={false}
          closeOnDimmerClick={false}
          onClose={() => setOpenEdit(false)}
          onOpen={() => setOpenEdit(true)}
          open={openEdit}
          size="big"
        >
          <Modal.Header>{activeItem.name}</Modal.Header>
          <Modal.Content image>
            <Image
              size="medium"
              src={activeItem.src}
              id={activeItem.id}
            ></Image>
            <Modal.Description>
              <Dropdown
                placeholder="Categoria"
                defaultValue={activeItem.cat}
                search
                selection
                options={listaCat}
                onChange={(e, { value }) => {
                  setActiveItem((prevObject) => ({
                    ...prevObject,
                    name: activeItem.name,
                    price: activeItem.price,
                    src: activeItem.src,
                    id: activeItem.id,
                    cat: value,
                    cant: activeItem.cant,
                    bodega: activeItem.bodega,
                  }));
                }}
              />
              <p>
                {activeItem.price
                  ? currencyFormat(activeItem.price)
                  : activeItem.price}
              </p>
              <span>Inventario (Disponible - Bodega)</span>
              <p style={{ display: "flex" }}>
                <Input
                  type="number"
                  min="0"
                  size="mini"
                  defaultValue={activeItem.cant}
                  onChange={(e, { value }) => {
                    setActiveItem((prevObject) => ({
                      ...prevObject,
                      name: activeItem.name,
                      price: activeItem.price,
                      src: activeItem.src,
                      id: activeItem.id,
                      cat: activeItem.cat,
                      cant: value,
                      bodega: activeItem.bodega,
                    }));
                  }}
                ></Input>
                <Button icon="angle left" />
                <Input
                  type="number"
                  min="0"
                  size="mini"
                  defaultValue={activeItem.bodega}
                  onChange={(e, { value }) => {
                    setActiveItem((prevObject) => ({
                      ...prevObject,
                      name: activeItem.name,
                      price: activeItem.price,
                      src: activeItem.src,
                      id: activeItem.id,
                      cat: activeItem.cat,
                      cant: activeItem.cant,
                      bodega: value,
                    }));
                  }}
                ></Input>
              </p>

              <span>Precio</span>
              <p>
                <Input
                  type="number"
                  min="0.01"
                  size="mini"
                  defaultValue={activeItem.price}
                  onChange={(e, { value }) => {
                    setActiveItem((prevObject) => ({
                      ...prevObject,
                      name: activeItem.name,
                      price: value,
                      src: activeItem.src,
                      id: activeItem.id,
                      cat: activeItem.cat,
                      cant: activeItem.cant,
                      bodega: activeItem.bodega,
                    }));
                  }}
                ></Input>
              </p>

              <span>Imagen</span>
              <p>
                <Input
                  id="anexoImg"
                  fluid
                  className="input-image"
                  type="file"
                  onChange={(e) => {
                    handleImageUpload(e, activeItem.id);
                    setActiveItem((prevObject) => ({
                      ...prevObject,
                      name: activeItem.name,
                      price: precio ? precio : activeItem.price,
                      src: activeItem.src,
                      id: activeItem.id,
                      cat: activeItem.cat,
                      cant: cant ? cant : activeItem.cant,
                      bodega: activeItem.bodega,
                    }));
                  }}
                />
              </p>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button
              //disabled={activeItem.disponible <= 0}
              color="green"
              onClick={() => {
                registrar();
                setOpenEdit(false);
              }}
            >
              Actualizar
            </Button>
            <Button
              color="red"
              onClick={() => {
                setOpenEdit(false);
              }}
            >
              Cancelar
            </Button>
          </Modal.Actions>
        </Modal>
      )}

      <Modal open={open}>
        <Modal.Header>Nuevo Producto</Modal.Header>
        <Modal.Content image>
          <Image size="medium" id="nuevoProd" src={previewImg} wrapped />
          <Modal.Description>
            <Dropdown
              placeholder="Categoria"
              search
              selection
              options={listaCat}
              onChange={(e, { value }) => {
                setNewPCat(value);
              }}
            />
            <p>NOMBRE</p>
            <Input
              type="text"
              onChange={(e) => {
                setNombre(e.target.value);
              }}
            ></Input>
            <p>DISPONIBLES</p>
            <Input
              type="number"
              size="mini"
              onChange={(e) => {
                setCant(e.target.value);
              }}
            ></Input>
            <p>PRECIO</p>
            <Input
              labelPosition="right"
              type="number"
              size="mini"
              placeholder="Amount"
              onChange={(e) => {
                setPrecio(e.target.value);
              }}
            >
              <Label basic>$</Label>
              <input
                type="number"
                onChange={(e) => {
                  setPrecio(e.target.value);
                }}
              />
            </Input>
            <p>IMAGEN</p>
            <Input
              id="anexoImg"
              fluid
              className="input-image"
              type="file"
              onChange={(e) => {
                handleImageUpload(e, "nuevoProd");
              }}
            />
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button color="red" onClick={() => setOpen(false)}>
            Cancelar
          </Button>
          <Button
            content="Guardar"
            labelPosition="right"
            icon="checkmark"
            onClick={() => {
              setOpen(false);
              registrarNuevo();
            }}
            positive
          />
        </Modal.Actions>
      </Modal>

      <Segment style={{ display: "flex", height: "10vh" }}>
        {categorias.map((cat) => (
          <p style={{ margin: "auto" }}>
            <Popup
              content={cat.name}
              key={cat.name}
              trigger={
                <Image
                  src={cat.src}
                  size="mini"
                  onClick={() => {
                    categoria ? setCat("") : setCat(cat.name);
                  }}
                />
              }
            />
            <span style={{ color: cat.name === categoria ? "red" : "black" }}>
              {cat.name}
            </span>
          </p>
        ))}
      </Segment>
      <div style={{ display: "flex" }}>
        <Input
          icon="search"
          placeholder="Buscar..."
          size="small"
          onChange={(e, { value }) => {
            setBuscar(value);
          }}
        />
        <Button
          icon="add"
          content="Nuevo"
          color="green"
          onClick={() => {
            setOpen(true);
          }}
        />
      </div>
      <Segment style={{ height: "50vh", overflowY: "scroll" }}>
        {data && (
          <Table basic="very" celled collapsing>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Producto</Table.HeaderCell>
                <Table.HeaderCell>Disponibles</Table.HeaderCell>
                <Table.HeaderCell>Bodega</Table.HeaderCell>
              </Table.Row>
            </Table.Header>

            <Table.Body>
              {data
                .sort(compare)
                .filter((item) => {
                  return (
                    item.Product_Name.toLowerCase().includes(
                      buscar ? buscar.toLowerCase() : buscar
                    ) &&
                    item.Categoria.toLowerCase().includes(
                      categoria ? categoria.toLowerCase() : categoria
                    )
                  );
                })
                .map((item) => (
                  <Table.Row>
                    <Table.Cell>
                      <Header as="h4" image>
                        <Image src={item.Image} rounded size="mini" />
                        <Header.Content>
                          {item.Product_Name}
                          <Header.Subheader>
                            {currencyFormat(item.Price)}
                          </Header.Subheader>
                        </Header.Content>
                      </Header>
                    </Table.Cell>
                    <Table.Cell>{item.Disponibles}</Table.Cell>
                    <Table.Cell>{item.Bodega}</Table.Cell>

                    <Table.Cell>
                      <a
                        href="#"
                        onClick={() => {
                          setActiveItem((prevObject) => ({
                            ...prevObject,
                            name: item.Product_Name,
                            price: item.Price,
                            src: item.Image,
                            id: item.Product_Id,
                            cant: item.Disponibles,
                            bodega: item.Bodega,
                            cat: item.Categoria,
                          }));
                          setOpenEdit(true);
                        }}
                      >
                        Editar
                      </a>
                    </Table.Cell>
                  </Table.Row>
                ))}
            </Table.Body>
          </Table>
        )}
      </Segment>
    </Container>
  );
}
